<template>
  <div class="emulator-container">
    <div ref="containerRef"></div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted, watch, nextTick } from 'vue'

interface Props {
  url: string
  gameName: string
  width?: number
  height?: number
}

const props = withDefaults(defineProps<Props>(), {
  width: 800,
  height: 600
})

const containerRef = ref<HTMLElement>()

// 完全按照dist版本的run函数
async function run(url: string, gameName: string) {
  if (!containerRef.value) return
  
  // 清除之前的游戏
  containerRef.value.innerHTML = ''
  
  const parts = url.split(".")
  const ext = parts.pop()?.toLowerCase()

  const core = await (async (ext) => {
    if (["fds", "nes", "unif", "unf"].includes(ext || ''))
      return "nes"

    if (["smc", "fig", "sfc", "gd3", "gd7", "dx2", "bsx", "swc"].includes(ext || ''))
      return "snes"

    if (["z64", "n64"].includes(ext || ''))
      return "n64"

    if (["pce"].includes(ext || ''))
      return "pce"

    if (["ngp", "ngc"].includes(ext || ''))
      return "ngp"

    if (["ws", "wsc"].includes(ext || ''))
      return "ws"

    if (["col", "cv"].includes(ext || ''))
      return "coleco"

    if (["d64"].includes(ext || ''))
      return "vice_x64sc"

    if (["nds", "gba", "gb", "z64", "n64"].includes(ext || ''))
      return ext

    // 默认返回nes
    return "nes"
  })(ext)

  const div = document.createElement("div")
  const sub = document.createElement("div")
  const script = document.createElement("script")

  sub.id = "game"
  div.id = "display"

  div.appendChild(sub)
  containerRef.value.appendChild(div)

  window.EJS_player = "#game";
  window.EJS_gameName = gameName;
  window.EJS_biosUrl = "";
  window.EJS_gameUrl = url;
  window.EJS_core = core;
  window.EJS_pathtodata = "/data/";
  window.EJS_startOnLoaded = true;
  window.EJS_DEBUG_XX = true;  // 使用非压缩版本
  window.EJS_disableDatabases = true;
  window.EJS_threads = false;

  script.src = "/data/loader.js";
  document.body.appendChild(script);
}

// 启动游戏
const startGame = async () => {
  if (!props.url) return
  await run(props.url, props.gameName || 'Game')
}

// 停止游戏
const stopGame = () => {
  if (containerRef.value) {
    containerRef.value.innerHTML = ''
  }
  // 清理全局变量
  delete (window as any).EJS_player
  delete (window as any).EJS_gameName
  delete (window as any).EJS_biosUrl
  delete (window as any).EJS_gameUrl
  delete (window as any).EJS_core
  delete (window as any).EJS_pathtodata
  delete (window as any).EJS_startOnLoaded
  delete (window as any).EJS_DEBUG_XX
  delete (window as any).EJS_disableDatabases
  delete (window as any).EJS_threads
}

// 监听URL变化
watch(() => props.url, (newUrl) => {
  if (newUrl) {
    nextTick(() => {
      startGame()
    })
  } else {
    stopGame()
  }
}, { immediate: false })

onMounted(() => {
  if (props.url) {
    nextTick(() => {
      startGame()
    })
  }
})

onUnmounted(() => {
  stopGame()
})

// 暴露方法给父组件
defineExpose({
  startGame,
  stop: stopGame
})
</script>

<style scoped>
.emulator-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#emulator {
  width: 100%;
  height: 100%;
  max-width: 800px;
  max-height: 600px;
}
</style>
